<template>
    <scroll ref="scrollRef" :data="result" :pullup="pullup" @scrollToEnd="scrollToEnd" :beforeScroll="beforeScrollData"
             @beforeScroll="beforeScroll" class="suggest-list">
        <ul class="suggest-lists">
            <li class="suggest-item" v-for="item in result" @click="selectItem(item)">
                <div class="suggest-icon">
                    <i :class="getIconCls(item)"></i>
                </div>
                <div class="suggest-name">
                    <p class="suggest-text" v-html="getSingernameOrSongname(item)"></p>
                </div>
            </li>
            <loading v-show="hasMore" title="..."></loading>
        </ul>
        <div class="no-result-wrapper" v-if="!hasMore && !result.length">
            <noresult></noresult>
        </div>
    </scroll>
</template>
<script>
import { search } from "../api/search.js";
import { createSong } from "../common/js/song.js";
import  Singer  from "../common/js/Singer.js";
import loading from "../base/loading";
import scroll from "../base/scroll";
import noresult from "../base/noresult";
import { mapMutations, mapActions } from "vuex";
const TYPE_SINGER = "singer";
export default {
  data() {
    return {
      // 
      page: 1,
      // 
      perpage: 20,
      // 
      result: [],
      // 
      hasMore: true,
      beforeScrollData: true,
      pullup: true
    };
  },
  props: {
    query: {
      type: String,
      default: ""
    },
    zhida: {
      type: Boolean,
      default: true
    }
  },
  watch: {
    query(newVal) {
      this.search();
      console.log("");
    }
  },
  methods: {
     ...mapMutations({
      'setSinger': 'SET_SINGER'
    }),
    ...mapActions(['insertSong']),
    selectItem(item){
        console.log(item);
        if(item.type === TYPE_SINGER){
            let singer = new Singer({
                id:item.singermid,
                name:item.singername
            })
            console.log(singer);
            this.$router.push({
                 path: `/search/${singer.id}`
            })
            this.setSinger(singer);
        } else{
            this.insertSong(item);
        }
        console.log('search');
        this.$emit('select');
    },
    getIconCls(item) {
      if (item.type === TYPE_SINGER) {
        return "suggest-list-singer";
      } else {
        return "suggest-list-song";
      }
    },
    getSingernameOrSongname(item) {
      if (item.type === TYPE_SINGER) {
        console.log("");
        return item.singername;
      } else {
        return `${item.name}-${item.singer}`;
      }
    },
    scrollToEnd() {
      console.log("");
      if (!this.hasMore) {
        //
        return;
      }
      this.page++;
      search(this.query, this.page, this.perpage, this.zhida).then(res => {
        if (res.code === 0) {
          console.log(res);
          //
          this.result = this.result.concat(this._formatSearch(res.data));
          console.log(this.result);
          this._checkMore(res.data);
          console.log(this.hasMore);
        }
      });
    },
    beforeScroll() {
      this.$emit("beforeScroll");
    },
    search() {
      console.log(this.query, this.page, this.perpage, this.zhida);
      this.page = 1;
      this.hasMore = true;
      search(this.query, this.page, this.perpage, this.zhida).then(res => {
        if (res.code === 0) {
          console.log(res);
          //
          this.result = this._formatSearch(res.data);
          console.log(this.result);
          this._checkMore(res.data);
          console.log(this.hasMore);
        }
      });
    },
    _formatSearch(data) {
      //
      let ret = [];
      if (data.zhida && data.zhida.singerid) {
        //
        ret.push({ ...data.zhida, ...{ type: TYPE_SINGER } });
      }
      if (data.song) {
        ret = ret.concat(this._normaliszeSongs(data.song.list));
      }
      return ret;
    },
    _normaliszeSongs(list) {
      let ret = [];
      list.forEach(item => {
        if (item.songid && item.albummid) {
          ret.push(createSong(item));
        }
      });
      return ret;
    },
    //
    _checkMore(data) {
      let song = data.song;
      if (
        !song.list.length ||
        song.curnum + song.curpage * this.perpage > song.totalnum
      ) {
        this.hasMore = false; //
      }
    },
    refresh() {
      this.$refs.scrollRef.refresh(); //
    }
  },
  components: {
    scroll,
    loading,
    noresult
  }
};
</script>
<style>
.suggest-list {
  height: 100%;
  overflow: hidden;
  border-top: 1px solid #00000026;
}
.suggest-lists {
  padding: 0 30px;
}
.suggest-item {
  display: flex;
  align-items: center;
  padding: 15px 0;
  border-bottom: 1px solid #00000026;
}
.suggest-icon {
  flex: 0 0 30px;
}
.suggest-name {
  margin-left: 15px;
  flex: 1;
  font-size: 16px;
  color: #000;
  overflow: hidden;
}
.suggest-list-singer {
  display: inline-block;
  width: 30px;
  height: 30px;
  background-image: url(../common/image/suggest-list-singer.svg);
}
.suggest-list-song {
  display: inline-block;
  width: 30px;
  height: 30px;
  background-image: url(../common/image/suggest-list-song.svg);
}
.no-result-wrapper{
    position: absolute;
    width: 100%;
    top: 50%;
    transform:translateY(-50%);
}
</style>



